import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import {Data} from "./components/Data";
import Detail from "./route/Detail"
import Search from "./route/Search";
import Home from "./route/Home";

function App() {
  return(
    <Data>
      <Router>
        <Switch>          

          <Route path="/movie/:id">
            <Detail />
          </Route>
          <Route path="/movie">
            <Home/>
          </Route> 
                   <Route path="/">
            <Search />
          </Route>
        </Switch>
    </Router>
    </Data>

  );
}

export default App;
