import styles from "../style/Search.module.css";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import {MovieContext} from "../components/Data";
import { useContext } from "react";
import { useState } from "react";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";


function Search(){
    const {movies,loading}=useContext(MovieContext);
    const [search,setSearch]=useState("");
    const history=useHistory();
    const onChange=(event)=>{
        setSearch(event.target.value);
    }

    
    const filterTitle= movies.filter((movie)=>{
        return movie.title.replace(/\s+/g, "").toLowerCase().includes(search.replace(/\s+/g, "").toLowerCase());
   })

    const onSubmit=(event)=>{
        event.preventDefault();

        console.log(search);     

        if(filterTitle){
            const movieID=filterTitle.map((movie)=>{
                return movie.id
            }
            )
            history.push(`/movie/${movieID}`)
        }
        else{
            alert("Movie not found!");
        }
        
        console.log(filterTitle);
    }

    return(
        <div>
        {loading ? <h1>Loading...</h1>:
            <div>
                <h1 className={styles.title}>Search Movie!</h1>

                <form onSubmit={onSubmit} className={styles.searchContainer}>
                    <input value={search} onChange={onChange} placeholder="Enter movie name..."/>
                    <button type="submit"> Click</button>
                </form>

                <div className={styles.searchContainer}>
                    <Link to={'/movie'} style={{textDecoration:"none"}}>
                        movie index
                    </Link>
                </div>
                
                <div className={styles.outContainer}>
                    <div className={styles.container}>{filterTitle.map(movie=> <p>{movie.title} </p> )}</div>
                </div>
            
            </div>}
        </div>
    );
}

export default Search;